import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import( '../views/HomeView.vue')
  },
  {
    path: '/about',
    name: 'about',
    component: () => import( '../views/AboutView.vue')
  },
    {
    path: '/zhuc',
    name: 'zhuc',
    component: () => import( '../views/ZhuCe.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
